import cls from './Cart.module.scss'
import { Link } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import React from 'react'

import { clearCart, selectorCart } from '../../redux/cart/Slice'

import CartItem from './CartItem'
import CartEmpty from './CartEmpty'
import { ReactComponent as TrashIcon } from '../../assets/img/trash.svg'
import { ReactComponent as BtnBackIcon } from '../../assets/img/grey-arrow-left.svg'
import { ReactComponent as CartIcon } from '../../assets/img/cart.svg'

const Cart: React.FC = () => {
	const dispatch = useDispatch()
	const { totalCount, products, totalPrice } = useSelector(selectorCart)

	const onClearCart = () => {
		if (window.confirm('–û—á–∏—Å—Ç–∏—Ç—å –∫–æ—Ä–∑–∏–Ω—É?')) {
			dispatch(clearCart())
		}
	}

	return (
		<>
			<div className='container container--cart'>
				{products[0] ? (
					<div className='cart'>
						<div className='cart__top'>
							<h2 className='content__title'>
								<CartIcon stroke='#000' />
								–ö–æ—Ä–∑–∏–Ω–∞
							</h2>
							<button onClick={onClearCart} className='cart__clear'>
								<TrashIcon />

								<span>–û—á–∏—Å—Ç–∏—Ç—å –∫–æ—Ä–∑–∏–Ω—É</span>
							</button>
						</div>
						<div className={cls.content__items}>
							{products.map((item, i) => (
								<CartItem key={i} {...item} />
							))}
						</div>
						<div className='cart__bottom'>
							<div className='cart__bottom-details'>
								<span>
									–í—Å–µ–≥–æ –ø–∏—Ü—Ü: <b>{totalCount} —à—Ç.</b>
								</span>
								<span>
									–°—É–º–º–∞ –∑–∞–∫–∞–∑–∞: <b>{totalPrice} ‚ÇΩ</b>
								</span>
							</div>
							<div className='cart__bottom-buttons'>
								<Link to='/' className='button button--outline button--add go-back-btn'>
									<BtnBackIcon />
									<span>–í–µ—Ä–Ω—É—Ç—å—Å—è {window.innerWidth > 516 ? '–Ω–∞–∑–∞–¥' : ''}</span>
								</Link>
								<button className='button pay-btn'>
									<span>–û–ø–ª–∞—Ç–∏—Ç—å {window.innerWidth > 516 ? '—Å–µ–π—á–∞—Å' : ''}</span>
								</button>
							</div>
						</div>
					</div>
				) : (
					<CartEmpty />
				)}
			</div>
		</>
	)
}

export default Cart
